<template>
  <!-- 네비 json 가지고와서 화면에 출력 네비 + 서브메뉴 네비 가져와서 화면에 출력 -->
  <!-- 헤더 -->
  <div class="w-full border-b h-20">
    <div class="max-w-7xl mx-auto h-full flex items-center">
      <!-- 로고 -->
      <div class="w-64 md:ml-8 absolute lg:static left-2/4 -translate-x-2/4 lg:left-0 lg:translate-x-0 flex items-center">
        <img :src="require(`@/assets/images/logo_img.png`)" alt="로고" class="ml-1 w-full">
        <img :src="require(`@/assets/images/logo.png`)" alt="로고" class="ml-1 w-full">
        <!-- 빌드할 때 require 바인딩으로 페이지에 포함시키기 -->
      </div>
      <div class="ml-20">
        <ul class="hidden lg:flex font-bold text-lg">
        <!-- shrink-0  크기를 고정하는 속성 -->
          <li class="shrink-0 xl:px-8 lg:px-5
          transition-all relative h-20 leading-[80px]
          after:absolute after:transition-all after:w-full after:h-0 after:bg-[#37a2d7]
          after:left-0 after:bottom-6 hover:after:h-[3px] 
          " v-for="(e, index) in NavList" :key="e" :class="NavSelectIndex == index && 'after:h-[3px] text-[#37a2d7]'" 
          @mouseout="NavSelectIndex = null" 
          @mouseover="NavSelectIndex = index; SubMenu(index)"
          >
          <!-- @mouseover="isShow = true" @mouseleave="isShow = false" -->
          <a href="#">{{ e }}</a>
          <ul class="absolute z-10 flex flex-wrap bg-white rounded-br-2xl rounded-bl-2xl -ml-5 transition-all duration-300 h-0 overflow-hidden" :style="NavSelectIndex == index && isSubMenu">
            <!-- tailwind에 없는 길이 값을 스크립트로 먹지 않으므로 html에 직접 입력해줌 -->
            <template v-for="el in SubList[index-1]" :key="el">
              <!-- template태그가 나오는 경우는 v-for/if가 필요하기 때문 -->
              <!-- 스크립트부터 로딩되므로 px단위로 길이값을 주면 동작이 되지않는다,
              ㅡ> 액션을 주지 않으면 데이터 값이 없는 상태이기 때문에 css값이 동작하지 않음 -->
              <li v-if="index != 0" class="text-xs shrink-0 basis-full text-center pt-5 last-of-type:pb-5 text-black hover:text-[#37a2d7]">
              <a href="#" @click.prevent>{{ el }}</a>
              </li>
            </template>
          </ul>
        </li>
        </ul>
      </div>
    </div>
      <div class="lg:ml-20 w-6 h-6 hidden sm:block">
        <img :src="require(`@/assets/images/icon_lan.png`)" alt="언어변경" class="w-full">
      </div>
      <div class="w-6 h-6 -ml-10 sm:hidden">
        <img :src="require(`@/assets/images/icon_search.png`)" alt="검색" class="w-full">
      </div>
    </div>
    
  <!-- 헤더 -->
  <!-- 햄버거 -->
  <div class="absolute right-4 md:right-8 top-6 duration-1000 transition-all z-50 cursor-pointer"
    @click="isOpen == true ? isOpen = false : isOpen = true"
    :class="isOpen && 'on'">
    <div v-for="e in 3" :key="e" class="w-[30px] h-0.5 bg-black rounded m-1.5 transition-all duration-1000 lg:hidden">
    </div>
  </div>
  <!-- 햄버거 -->
  <div class="w-80 h-full fixed bg-gray-50 z-40 -right-80 p-12 box-border transition-all 
    duration-500 top-0 lg:hidden overflow-y-scroll" :class="isOpen && '!right-0'">
    <!-- overflow-y-scroll 컨텐츠의 양이 영역을 넘어갈 때 스크롤 기능 추가 -->
    <!-- css에서는 동작하지만 tailwind에서는 위치 값이 0일 때 데이터가 없다고 판단해서 !를 붙여줘야함  -->
    <ul class="mt-12">
      <li class="py-5 border-b" v-for="(e, index) in NavList" :key="e"><a href="#" class="font-bold" @click="NavSelectIndex = index; SubMenu(index)">{{ e }}</a>
        <template v-for="(e,i) in SubList" :key="e">
          <ul v-if="index === i+1" class="pt-5 flex flex-wrap justify-between sub_list h-0 overflow-hidden transition-all duration-300" :style="NavSelectIndex === index && 'height:116px'">
            <!-- 민원센터의 하위메뉴가 없으므로 인덱스 값에 +1을 줘서 0123이 아닌 1234로 시작 -->
            <li v-for="el in e" :key="el" class="basis-2/4 pt-2 text-xs hover:text-[#37a2d7]">
              <a href="#">{{ el }}</a>
              <!-- 템플릿 태그에는 클래스,id값을 줄수 없음 -->
              <!-- v-if, v-for를 같이 써야할 때 for문을 템플릿 태그로 감싸서 새로 걸어야함, if,for는 같은 태그에 공존못함 -->
            </li>
          </ul>
        </template>  
      </li>
    </ul>
  </div>
  


</template>
<script>
import NavData from '../assets/Data.json'
export default {
  name: "NavPage",
  data() {
    return {
      isOpen: false, // 햄버거 메뉴 
      NavSelectIndex: null, // null 데이터가 비어있는 상태, none은 없애다라는 뜻
      SubMenuOpen: false,
      // isShow: false,
      NavList: NavData.Nav,
      SubList: NavData.SubMenu,
      isSubMenu : ''
    }
  },
  methods:{
    SubMenu(e){
      // console.log(e)

        if(e != 0){
          const list = document.querySelectorAll(".sub_list")[e-1];
          const length = list.querySelectorAll("li").length; 
          // console.log(length)
          this.isSubMenu = `height:${length*52}px`
          // 52 = 각각의 li들의 세로 길이 값

            if(list.style.height === '0px'){
              list.style.height = '116px';
            }else{
              list.style.height = '0px';
            }
        }
    }
  },
  windowWidth(el){ // 괄호 속 width는 작명. 
        // console.log(width) - 
        if(el < 1024){
          this.NavSelectIndex = null;
          this.isOpen = false
        }
      }
}
</script>
<style >
  .on div:nth-child(1){
    transform: rotate(45deg) translateY(12px);
  }
  .on div:nth-child(2){
    opacity:0;
    transform: translateX(-30px) rotate(720deg) ;
    /* scale(2) - bar가 회전하면서 빠져나갈 때 크기 조정 */
  }
  .on div:nth-child(3){
    transform: rotate(-45deg) translateY(-12px);
  }
</style>